<template>
  <div>
    <!-- 부모 컨포넌트 -->
    <!-- @send-message="sendMessage": 이벤트가 발생하면 부모컴포넌트의 sendMessage메서드가 호출됨 -->
    <child-component @send-message="sendMessage" ref="child_component" />
    <button type="button" @click="changeChildData">click</button>
  </div>
</template>
<script>
import ChildComponent from "./ChildComponent3.vue";
export default {
  components: { ChildComponent },
  methods: {
    changeChildData() {
      this.$refs.child_component.msg = "부모컴포넌트가 변경한 데이터";
    },
  },
};
</script>
